@import "../colors.module.scss";
@import "../media.module.scss";
@import "../globals.module.scss";

:global(#root) {
  height: 100%;
}

* {
  box-sizing: border-box;
}

.siteContent {
  position: relative;
  display: flex;
  height: 100%;
  min-height: 740px;

  @include small-screen-only {
    min-height: auto;
    transform: translateX(0);
    &.sideMenuOpened {
      transform: translateX($sideMenuWidth);
    }
  }
}

.pageRightWrapper {
  width: 100%;
  background: $grey01;
  position: relative;
}

.pageWrapper {
  width: 100%;
  height: 95%;
  border-radius: 10px 0 0 10px;
  background: $white;
  position: relative;
  overflow: auto;
  margin-top: 10px;
  box-shadow: 6px 4px 7px $grey18;
  opacity: 0;
  border: 1px solid $grey09;
  transform: translateX(65px);
  transition: all 320ms cubic-bezier(0.19, 1, 0.22, 1);
  will-change: transform, opacity;

  @include small-screen-only {
    margin: 0;
    border-radius: 0;
    box-shadow: none;
    height: 100%;
    box-shadow: none;
  }
}

.pageBasePlaceholder {
  position: absolute;
  width: 100%;
  height: 95%;
  border-radius: 10px 0 0 10px;
  background: $grey04;
  overflow: hidden;
  margin-top: 10px;
  box-shadow: 6px 4px 7px $grey18;

  @include small-screen-only {
    width: 100%;
    height: 100%;
    border-radius: 0;
    margin: 0;
    box-shadow: none;
  }
}

.slideInAnimation {
  opacity: 1;
  transform: translateX(0);
}

.slideOutAnimation {
  opacity: 0;
  transform: translateX(65);
}
