@mixin tiny-screen-only {
  @media (max-width: 480px) {
    @content;
  }
}

@mixin small-screen-only {
  @media (max-width: 768px) {
    @content;
  }
}

@mixin medium-screen-only {
  @media (max-width: 1024px) and (min-width: 768px) {
    @content;
  }
}

@mixin big-screen-only {
  @media (min-width: 1024px) {
    @content;
  }
}
