@import "../../components/colors.module.scss";
@import "../../components/media.module.scss";

.pageContentWrapper {
  display: flex;
  flex-flow: column;
  padding: 28px 50px;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  position: relative;

  @include small-screen-only {
    padding: 30px 20px;
  }
}

.nodeCardWrapper {
  margin-bottom: 15px;
}

.cardWrapperSettingsLoaded {
  height: 77vh;
}

.pageHeaderWrapper {
  display: inline-flex;
  width: 98.5%;
  justify-content: space-between;
  align-items: center;
  color: #4a515e;
}

.displayMasterNode {
  font-family: "Open Sans Bold", sans-serif;
  font-size: 14px;
  color: $grey05;
}

.displayMasterNode input {
  margin-left: 5px;
}

.nodeSettingsSpinner {
  display: inline-block;
  padding: 28px 35px 20px 21px;
}
.nodeSettingsSpinner:after {
  content: " ";
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid $grey05;
  border-color: $grey05 transparent $grey05 transparent;
  animation: nodeSettingsSpinner 1.2s linear infinite;
}

.loading {
  font-family: "Open Sans Bold", sans-serif;
  font-size: 16px;
  display: flex;
  flex-flow: column;
  align-items: center;
  padding-top: 100px;
}

.pageTitleCustomWrapper {
  display: flex;
  align-items: baseline;

  .pageTitle {
    line-height: 21px;
    padding-right: 10px;

    @include small-screen-only {
      padding-right: 5px;
    }
  }
}

.pageTitle {
  border-right: 2px solid $grey05;
  padding-right: 20px;
}
@keyframes nodeSettingsSpinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
