@import "../../../components/colors.module.scss";
@import "../../../components/media.module.scss";

$hoverTransition: border 200ms ease-in-out, box-shadow 200ms ease-in-out, background-color 200ms ease-in-out;

.optionsMenu {
  width: 78px;
  height: 33px;
  position: absolute;
  right: 2px;
  top: 7px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  color: $grey05;
  padding: 5px 6px 5px 6px;
  transition: $hoverTransition;

  @include small-screen-only {
    display: none;
  }
}

.optionsItem:hover {
  color: $grey01;
  cursor: pointer;
}

svg.optionsMenuDnd {
  color: $blueAlpha01;

  &:hover {
    color: rgba(0, 151, 189, 0.96);
  }
}

.appIsLoading {
  pointer-events: none;
}
