@import "../colors.module.scss";

$hoverTransition: border 200ms ease-in-out, box-shadow 200ms ease-in-out, background-color 200ms ease-in-out;

%thumbnailTextCommon {
  transition: $hoverTransition;
  background-color: $grey13;
  color: $grey05;
  line-height: 48px;
  display: flex;
  height: 100%;
  align-items: center;
  justify-content: center;
  text-shadow: none;
}

%wrapperCommon {
  display: inline-block;
  background-color: #fff;
  overflow: hidden;
}

.smallWrapper {
  width: 22px;
  height: 20px;
  border-radius: 3px;
  @extend %wrapperCommon;

  .thumbnailText {
    @extend %thumbnailTextCommon;
  }
}

.normalWrapper {
  width: 40px;
  height: 40px;
  border-radius: 6px;
  @extend %wrapperCommon;

  &.withDndStyles {
    .textThumbnailLarge {
      color: $white;
      background: $blue02;
    }
  }

  .thumbnailText {
    @extend %thumbnailTextCommon;
  }
}
