@import "../../../components/colors.module.scss";

.nodeSettingsWrapper {
  height: 100%;
  width: 100%;
  background: $grey10;
}

.nodeSettingsHeader {
  color: $grey11;
  font-size: 18px;
  font-family: "Open Sans Bold", sans-serif;
  width: 100%;
  padding: 10px 20px 6px;
  line-height: 18px;
  background: $grey15;
  border-radius: 8px 8px 0 0;
  border-bottom: 1px solid $grey16;
  box-shadow: 0 5px 15px $greyAlpha03;

  display: flex;
  align-items: center;
  justify-content: space-between;
}

.nodeSettingsTitle {
  background-color: $grey09;
}

.nodeSettingsHeader > div {
  display: inline-block;
}
.nodeSettingsHeaderDetails {
  font-size: 12px;
  font-family: "Open Sans Light", sans-serif;
  color: $grey11;
}

.nodeSettingsHeaderDetails:before {
  content: " | ";
  font-size: 14px;
  font-family: "Open Sans Light", sans-serif;
  color: $grey11;
  position: relative;
  bottom: 3px;
}

.nodeSettingsContent {
  height: calc(100% - 55px);
}

.loaderWrapper {
  display: flex;
  height: 100%;
  justify-content: center;
  align-items: center;
}

.closeButton {
  cursor: pointer;
}

.cardSettings {
  background: $grey10;
  border: 1px solid $grey16;
  border-radius: 10px;
  margin-bottom: 15px;
  box-shadow: 0 5px 30px $greyAlpha04;
  border: 1px solid $greyAlpha01;
  transition: all 600ms cubic-bezier(0.23, 1, 0.32, 1);
  z-index: 1;
}
