@import "../components/colors.module.scss";
$textShadow: 2px 2px 3px rgba(32, 44, 61, 0.2);
$sideMenuWidth: 230px;

html {
  box-sizing: border-box;
  margin: 0;
  height: 100%;
}

body {
  margin: 0;
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
}

%hoverUnderline {
  &:before {
    content: "";
    position: absolute;
    z-index: -1;
    left: 51%;
    right: 51%;
    bottom: 0;
    background: $blue03;
    height: 3px;
    transition-property: left, right;
    transition-duration: 200ms;
    transition-timing-function: ease-in-out;
  }

  &:hover:before,
  &.active:before {
    left: 0;
    right: 0;
  }

  &:hover,
  &.active {
    color: #334057;
    text-shadow: 2px 2px 3px rgba(32, 44, 61, 0.2);
  }
}
