@import "../../../components/globals.module.scss";
@import "../../../components/media.module.scss";

$iconHoverColor: #51bffb;

.actionMenuWrapper {
  position: relative;
}

.afaSortAmountDown {
  height: 13px;
}

.actionMenu {
  width: 145px;
  height: 100px;
  border: 1px solid $grey11;
  position: absolute;
  border-radius: 10px;
  background: $grey01;
  top: 36px;
  left: -55px;
  padding: 15px 0;
  box-shadow: 6px 4px 7px $greyAlpha03;

  .actionMenuItem {
    text-shadow: 2px 2px 3px $grey04;
    color: $grey07;
    font-family: "Open Sans Bold", sans-serif;
    font-weight: 300;
    display: block;
    font-size: 12px;
    position: relative;
    padding: 9px 25px;
    cursor: pointer;
    text-transform: uppercase;

    &.selected {
      background-color: #252e40;
      border-top: 1px solid $grey18;
      border-bottom: 1px solid $grey18;
      text-shadow: 2px 2px 3px $grey04;
      padding: 7px 25px;
      transition: background 100ms, color 100ms;
      color: #e6e9ef;
      background: $grey08;
    }

    &:hover {
      transition: background 100ms, color 100ms;
      color: #e6e9ef;
    }
  }

  &:after {
    content: "";
    transform: rotate(45deg);
    content: "";
    width: 14px;
    height: 14px;
    border: 7px solid #2b3649;
    transform: rotate(45deg);
    position: absolute;
    top: -4px;
    right: 23%;
  }
}

.viewFilterWrapper {
  position: relative;
  padding-left: 10px;
}

.selectedView {
  font-size: 12px;
  color: $grey05;
  text-shadow: 2px 2px 3px $greyAlpha05;
  padding-left: 5px;
  border-left: 2px solid $grey05;
  margin-left: 5px;
  line-height: 10px;

  @include small-screen-only {
    font-size: 10px;
    padding-top: 3px;
  }
}

.appear {
  opacity: 0.1;
}

.enterDone {
  transition: opacity 50ms ease-in-out;
  opacity: 1;
}
