@import "../colors.module.scss";
@import "../globals.module.scss";
@import "../media.module.scss";

$textColor: $grey11;
$textColorHover: $white;

.pageTitle {
  display: flex;
  border-bottom: 1px solid $grey09;
}

.pageTitleContent {
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 20px 57px 22px;
  position: relative;
  background: $grey15;
  z-index: 1;

  &.thin {
    padding: 15px 40px 13px;
  }

  @include small-screen-only {
    border-left: 1px solid $grey09;
    padding: 10px 20px 11px;
  }
}

.pageTitleName {
  font-size: 26px;
  font-family: "Open Sans Bold", sans-serif;
  display: flex;
  text-transform: uppercase;
  color: $textColor;
  flex-grow: 1;
  align-items: center;
  text-shadow: $textShadow;

  @include small-screen-only {
    font-size: 14px;
  }
}

.thin {
  .pageTitleName {
    font-size: 18px;
  }
}
.currentAppActions {
  color: $textColor;
  justify-content: center;
  align-items: center;
  display: flex;
}

.actionItem {
  cursor: pointer;
  height: 24px;
  margin-left: 10px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  position: relative;

  &:first-child {
    margin-left: 0;
  }

  @include small-screen-only {
    margin-left: 5px;

    text-shadow: $textShadow;
  }

  @extend %hoverUnderline;
}

.iconWrapper {
  padding-right: 5px;
}

.sideMenuHamburger {
  display: flex;
  align-items: center;
  padding: 10px 20px 11px;
  background: $grey16;
  justify-content: center;
  cursor: pointer;
}

.actionItemText {
  font-size: 14px;

  @include small-screen-only {
    font-size: 11px;
  }
}
