@import "../../../components/colors.module.scss";
@import "../../../components/media.module.scss";

$borderColor: $grey14;
$borderColorHovered: $greyAlpha01;

.cardWrapper {
  border: 1px solid $borderColor;
  border-radius: 10px;
  position: relative;
  overflow: hidden;
  height: 100%;
  transition: all 200ms ease-in-out;

  &:hover,
  &.hovered {
    box-shadow: 0 5px 30px $greyAlpha02;
    border: 1px solid $borderColorHovered;
  }

  &:hover {
    .expandTicker {
      border: 1px solid $borderColorHovered;
      border-bottom: 0;
      height: 17px;
      background-color: $grey13;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  &.disabled {
    pointer-events: none;
    .expandTicker {
      display: none;
    }
  }

  &.disabled:after {
    content: "";
    position: absolute;
    background: rgba(255, 255, 255, 0.7);
    width: 100%;
    height: 100%;
    top: 0;
  }
}

.nodeCardSummaryWrapper {
  background: $grey10;
  align-items: center;
  display: grid;
  height: 100%;
  grid-template-rows: 1fr;
  grid-template-columns: auto max-content minmax(max-content, 1fr) 1fr 135px auto;

  @include small-screen-only {
    grid-template-columns: minmax(20px, 1fr) minmax(max-content, 110px) auto;
  }
}

.section {
  padding: 0 25px;
  height: 50%;
  display: flex;
  align-items: center;
  border-right: 1px solid $grey16;

  @include small-screen-only {
    height: initial;
    padding: 0 10px;
  }
}

.cardSectionText {
  text-overflow: ellipsis;
  white-space: nowrap;
}

.IconSection {
  background-color: $grey13;
  width: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;

  @include small-screen-only {
    display: none;
  }
}

.deviceContentWrapper {
  display: flex;
  flex-flow: column;
  justify-content: space-evenly;
  align-items: flex-start;
}

.deviceType {
  @include small-screen-only {
    display: none;
  }
}

.deviceName,
.deviceId {
  & > span {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    width: 100%;
  }
}

.deviceId {
  @include small-screen-only {
    display: none;
  }
}

.deviceSettingsMenu {
  padding: 0 25px;
  align-items: flex-end;

  @include small-screen-only {
    padding: 0 10px;
  }
}

.deviceSettingsMenuIcon {
  padding: 2px 5px;
  transition: all 200ms ease-in-out;
  cursor: pointer;
  border: 1px solid transparent;
  vertical-align: middle;
  border-radius: 4px;
}

.deviceSettingsMenuIcon:hover {
  background: rgba(255, 255, 255, 1);
  border: 1px solid rgba(118, 128, 146, 0.32);
  box-shadow: 0px 3px 16px -3px rgba(118, 128, 146, 0.52);
}

.deviceStatus {
  @include small-screen-only {
  }
}

.nodeSettingPageWrapper {
  height: 66.5%;
  display: flex;
}

.placeHolderWidth {
  width: 78px;
}
.nodeSettingPage {
  width: 100%;
  margin: 0 25px;
  border-top: 1px solid $grey16;
}

.modalWrapper {
  border: 1px solid $greyAlpha01;
  min-width: 98.9%;
  height: 100%;
  border-radius: 8px;
  padding: 0;
  display: flex;
  margin: 0;
  flex-direction: column;
}

.expandTicker {
  overflow: hidden;
  border: 1px solid $borderColor;
  border-bottom: 0;
  margin: auto;
  height: 7px;
  width: 350px;
  text-align: center;
  border-radius: 10px 10px 0 0;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: $grey13;
  transition: all 200ms ease-out;
  cursor: pointer;
}
