@mixin SideMenuMediumScreensStyles() {
  .pinnedEmptyState {
    display: none;
  }

  .pinnedApp {
    .pinnedAppText {
      display: none;
    }

    .pinnedAppTextSmallScreen {
      display: block;
      font-size: 14px;
    }

    .pinnedContentWrapper {
      justify-content: center;
    }

    .sideMenuItem {
      padding: 10px 16px;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  .sideMenuWrapper {
    min-width: 75px;
  }

  .sideMenuTitle {
    padding: 10px 10px;
    text-align: center;
  }

  .sideMenuItemIcon {
    margin-right: initial;
  }

  .menuItemText {
    display: none;
  }

  .loggedInUserSectionWrapper {
    .userName {
      display: none;
    }

    .userNameCollapsed {
      color: $grey05;
      text-shadow: 2px 2px 3px $grey04;
      display: initial;
      width: 50px;
      &:hover {
        color: #e6e9ef;
        cursor: pointer;
      }
    }

    .userThumbnail {
      width: 50px;
      height: 50px;
    }
  }
}
