@import "../../../components/colors.module.scss";
@import "../../../components/globals.module.scss";
@import "../../../components/media.module.scss";

@mixin dndActionButton($color) {
  color: $grey05;
  cursor: pointer;
  position: relative;

  &:hover {
    color: $grey17;
    text-shadow: 2px 2px 3px $greyAlpha05;
  }

  &:hover .cancelIcon {
    color: $color;
  }
}

.rearrangeButtonWrapper {
  width: 110px;
  height: 100%;
  transition: 200ms width cubic-bezier(0.075, 0.82, 0.165, 1);
  overflow: hidden;

  @include small-screen-only {
    display: none;
  }
}

.rearrangeButtonWrapperInnter {
  min-width: 220px;
  height: 100%;
  display: flex;
  align-items: center;
}

.cancelIcon {
  height: 11px;
  width: 15px;
  margin-bottom: 1px;
}

.saveIcon {
  width: 18px;
  height: 11px;
  margin-bottom: 1px;
}

.cancelDNDState {
  @include dndActionButton($red01);
}

.saveDNDState {
  margin: 0 5px 0 10px;
  @include dndActionButton($red01);

  &:hover .saveIcon {
    color: $green01;
  }
}

.faArrowsAltV {
  position: relative;
  margin-right: 7px;
  margin-left: 5px;
  transform: rotate(45deg);
}

.saveCancelAppsOrder {
  transition: 400ms opacity cubic-bezier(0.075, 0.82, 0.165, 1);
  opacity: 0;
}

.enterDone {
  width: 220px;
}

.appear {
  opacity: 0.1;
}

.saveCancelAppsOrderEnterDone {
  opacity: 1;
}
