@import "../colors.module.scss";

.pinnAppWrapper {
  cursor: pointer;

  &:hover {
    color: $grey01;
    cursor: pointer;
  }
}

.pinnedIcon {
  transform: rotate(45deg);
}

.pinned {
  transition: 200ms transform cubic-bezier(0.075, 0.82, 0.165, 1);
  transform: rotate(0deg);
  color: $green01;

  &:hover {
    color: #00cca0;
  }
}
