@import "../colors.module.scss";
@import "./SideMenuBigScreens.module.scss";
@import "../globals.module.scss";

@mixin SideMenuSmallScreensStyles() {
  @include SideMenuBigScreensStyles();

  .faEllipsisV {
    color: $blue03;
  }

  .sideMenuWrapper {
    position: fixed;
    transform: translateX(-$sideMenuWidth);
    &.opened {
      transform: translateX(0);
    }
  }
}
