@import "../../components/colors.module.scss";
@import "../../components/media.module.scss";

$borderColor: $grey16;
$borderColorHovered: $greyAlpha01;
$hoverTransition: border 200ms ease-in-out, box-shadow 200ms ease-in-out, background-color 200ms ease-in-out;

.pageTitleWrapper {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

.pageContentWrapper {
  padding: 35px 56px 25px;
  width: 100%;
  height: 82vh;
  display: grid;
  grid-template-columns: repeat(auto-fill, 185px);
  grid-gap: 20px;
  grid-template-rows: repeat(auto-fill, 100px);

  @include small-screen-only {
    padding: 24px 6px;
    grid-gap: unset;
    grid-template-rows: repeat(auto-fill, 90px);
    grid-template-columns: repeat(auto-fill, 85px);
  }
}
