@import "../../../components/colors.module.scss";
@import "../../../components/media.module.scss";

$hoverTransition: border 200ms ease-in-out, box-shadow 200ms ease-in-out, background-color 200ms ease-in-out;

.applicationTile {
  height: 100px;
  width: 185px;
  border: 1px solid $grey09;
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 15px 10px;
  border-radius: 10px;
  background-color: $grey10;
  transition: $hoverTransition;

  &.notAvailable {
    pointer-events: none;
    border: 1px solid $grey10;
  }

  &.withDndStyles {
    transition: $hoverTransition;
    transition: none;
    border: 1px solid $blueAlpha01;
    background: $blue01;
    box-shadow: 5px 5px 20px $greyAlpha02;
    opacity: 1;
    cursor: move;
  }

  &:hover,
  &.appIsLoading {
    box-shadow: 5px 5px 20px $greyAlpha02;
    border: 1px solid $greyAlpha01;
  }

  &:hover {
    .openApplication {
      display: flex;
      background: $grey16;
      border: 1px solid $greyAlpha01;
      border-right: 0;
      position: absolute;
      right: 0px;
      width: 100px;
      height: 24px;
      bottom: 10px;
      justify-content: center;
      align-items: center;
      border-radius: 6px 0 0 6px;

      &:hover {
        cursor: pointer;
        background: $grey13;
        color: $grey09;
      }

      @include small-screen-only {
        display: none;
      }
    }
  }

  @include small-screen-only {
    height: 86px;
    width: auto;
    border: none;
    background: none;
    padding: 0;
    justify-content: flex-start;
    position: relative;

    &:hover,
    &.appIsLoading {
      box-shadow: none;
      border: none;
    }

    &.notAvailable {
      pointer-events: none;
      border: none;
    }
  }
}

.openApplication {
  display: none;
}

.dndPlaceHolder {
  content: "";
  display: block;
  height: 100px;
  width: 185px;
  border-radius: 10px;
  border: 3px dashed $blue02;
  position: absolute;

  @include small-screen-only {
    display: none;
  }
}

.applicationTileOuter {
  display: grid;
  grid-template-columns: repeat(auto-fill, 180px);
  grid-gap: 20px;
  transition: $hoverTransition;

  @include small-screen-only {
    grid-template-columns: repeat(auto-fill, 85px);
  }
}

.thumbnailWrapper {
  position: absolute;
  top: 10px;
  left: 10px;

  @include small-screen-only {
    position: relative;
    top: 0;
    left: 0;
    margin: auto;
  }
}

.applicationName {
  text-shadow: 2px 2px 3px rgba(32, 44, 61, 0.2);
  color: $grey12;
  margin-left: 2px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;

  @include small-screen-only {
    height: 32px;
    white-space: unset;
    text-align: center;
  }
}

.loader {
  background: rgb(236, 237, 244);
  position: absolute;
  top: 0px;
  left: 0px;
  border-radius: 10px;
  height: 98px;
  width: 183px;
}

.notAvailablePlaceholder {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: $greyAlpha03;
  border-radius: 10px;

  @include small-screen-only {
    background: $whiteAlpha01;
  }
}
